import React from "react";
import { useState } from 'react';
import { Container, Row, Col,Form, Button,Alert,Carousel } from "react-bootstrap";
import Particle from "../Particle";

function Registro() {
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [message, setMessage] = useState('');

  const handleSubmit = (event) => {
    event.preventDefault();
  };

  return (
    <Container fluid className="pedidos">
      <Container>
      <Col md={6}>
        <h1 className="project-heading">
          !<strong className="black">REGISTRATE ! </strong>
        </h1>
        <p>Es Necesario tener una cuenta para contactar con Centro de Salud Villa Copacabana y poder guardar cita</p>
        <Form onSubmit={handleSubmit}>
          <Form.Group className="mb-3" controlId="formName">
            <Form.Label>Nombre</Form.Label>
            <Form.Control
              type="text"
              placeholder="Ingrese su nombre"
              value={name}
              onChange={(e) => setName(e.target.value)}
            />
          </Form.Group>

          <Form.Group className="mb-3" controlId="formEmail">
            <Form.Label>Correo electrónico</Form.Label>
            <Form.Control
              type="email"
              placeholder="Ingrese su correo electrónico"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
            />
          </Form.Group>
          <Button variant="primary" type="submit">
            Enviar
          </Button>
        </Form>
        </Col>
      </Container>
    </Container>
  );
}

export default Registro;
